.WrapperBack {
  padding: 1% 0 2%;
  background-color: transparent;
}
.Wrapper {
  display: flex;
  align-items: center;
  justify-content: end;
}

.border {
  border-top: 1px solid black;
}
.Wrapper a {
  text-decoration: none;
  font-size: 22px;
  color: #0097a9;
  display: flex;
  align-items: center;
  justify-content: space-between !important ;
  width: 420px;
  text-align: end;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
}
.Wrapper h4 {
  background-color: #eeeeee;
  color: #0097a9;
  padding: 7px 10px;
  border-radius: 50%;
  text-decoration: none;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Wrapper span:hover {
  text-decoration: underline !important;
}

@media screen and (max-width: 500px) {
  .Wrapper a {
    width: 320px !important;
    font-size: 14px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between !important;
    padding: 0 !important;
  }
  .Wrapper span {
    width: 90% !important;
    margin-right: 3%;
  }
}
