.wrapperBack {
  background-image: url(../../../assets/belong/be.png);
  background-repeat: no-repeat;
  background-size: cover;
  padding: 5% 0;
}

.Wrapper {
  background: #004b79e5;
  width: 48%;
  padding: 3%;
  color: white;
  margin-top: 20%;
}

.Wrapper h2 {
  font-family: "ALS Agrofont";
  font-style: normal;
  font-weight: 700;
  font-size: 52px;
}

.Wrapper p {
  font-size: 26px;
  /* width: 60%; */
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
}

@media screen and (max-width: 955px) {
  .Wrapper {
    width: 80%;
  }
}

@media screen and (max-width: 660px) {
  .Wrapper {
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .Wrapper h2 {
    font-size: 39px;
  }
  .Wrapper P {
    font-size: 18px;
  }
  .Wrapper {
    width: 94%;
    padding: 5% 3%;
  }
}
