.wrapperBack {
  padding: 6% 0;
  background: #e3eeff;
}
.sliderItem {
  border: 1px solid white;
  border-radius: 30px;
  background: white;
  width: 90%;
  margin-left: auto !important;
  padding: 7%;
}

.subtext {
  margin: 0;
  font-size: 32px;
  border-bottom: 1px solid black;
  padding-bottom: 5%;
  font-family: "ALS Agrofont";
  font-style: normal;
  font-weight: 700;
  letter-spacing: 1px;
}
.bottomP {
  z-index: 1000 !important;
  background-color: #0066cc;
  color: white;
  font-size: 32px;
  border-radius: 50%;
  text-align: center;
  border: none;
  padding: 8px 17px;
  margin-left: 47%;
  margin-top: 7%;
  position: relative;
  font-family: "ALS Agrofont";
  
}
.bottomP::before {
  content: "";
  position: absolute;
  border: 1px solid #0066cc;
  width: 1000px;
  top: 50%;
  height: 0px;
  z-index: -10 !important;
}
.bottomP::after {
  content: "";
  position: absolute;
  border: 1px solid #0066cc;
  width: 1000px;
  top: 50%;
  left: -900%;
  height: 0px;
  z-index: -10 !important;
}

.bottomdiv {
  margin-top: 5%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.bottomdiv h3 {
  font-size: 22px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  margin: 0;
}

.bottomdiv p {
  font-size: 50px;
  margin: 0;
  color: #0066cc;
  background-color: #e5eeff;
  padding: 14px 15px 6px;
  border-radius: 50%;
}
.text {
  margin-bottom: 65px;
  font-size: 52px;
  font-family: "ALS Agrofont";
  font-style: normal;
  font-weight: 700;
  letter-spacing: 1px;
}

@media screen and (max-width: 500px) {
  .text {
    font-size: 39px;
    margin-left: 3%;
    margin-bottom: 35px;
  }
  .subtext {
    font-size: 22px;
  }
  .bottomdiv h3 {
    font-size: 13px;
  }
  .bottomdiv p {
    font-size: 30px;
    padding: 11px 12px 6px;
  }
  .sliderItem {
    width: 69%;
    margin: 0 auto !important;
  }
  .bottomP {
    margin-left: 44%;
    font-size: 35px;
    padding: 8px 16px 5px 17px;
  }
}
